<template>
   <div class="sellbill">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>我的卖单</span>
       </div>
       <div class='pay_cen'>
           <div class='pay_but'
           :class='{current:index === showIndex}'
           :key='index' v-for='(item,index) in arr'
           @click='cut(index,$event)'>
               {{item.title}}
           </div>
       </div>
       <div class='center'>
           <components :is='Tab'></components>
       </div>
       <van-button class='sell_btn' @click='sellbtn()' size="large">创建卖单</van-button>
   </div>
</template>

<script>

import Total from './SellBill/Total'  // 全部
import Await from './SellBill/Await'  // 待交易
import Confirm from './SellBill/Confirm'  // 待确定
import Accoplish from './SellBill/Accomplish'  // 已完成 

export default {
   name: 'sellbill',
    components: { Total,Await,Confirm,Accoplish },
   data() {
       return {
           Tab:Total,
           arr:[
               { title:'全部' },
               { title:'待交易' },
               { title:'待确定' },
               { title:'已完成' }
           ],
           showIndex:0
       }
   },
   methods:{
        retreat(){
            this.$router.go(-1);
        },
        sellbtn(){
            this.$router.push('/sale');
        },
        cut(index,e){
            this.showIndex = index;
            if( index === 0 ){
                this.Tab = Total;
            }else if( index === 1 ){
                this.Tab = Await;
            }else if( index === 2 ){
                this.Tab = Confirm;
            }else if( index === 3 ){
                this.Tab = Accoplish;
            }
        }
   },
}
</script>

